export default function randomIndex(arrayLength) {
  function getRandomIntExclusiveMax(min, max) {
    min = Math.ceil(min)
    max = Math.floor(max)
    return Math.floor(Math.random() * (max - min)) + min //The maximum is exclusive and the minimum is inclusive
  }
  return getRandomIntExclusiveMax(0, arrayLength)
}



